.courses {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.courses-header {
    margin-top: 1rem;
    /* font-family: 'DM Sans', sans-serif; */
    background: linear-gradient(270deg, #DBA6F4 11.87%, #A981FE 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
}

.courses-arrow {
    width: min(24rem, 50vw);
}

.courses-main {
    margin: 4rem 0 8rem 0;
    /* width: 60rem; */
    width: min(60rem, 70vw);
    display: grid;
    gap: min(1rem, 1vw);
    grid-template-areas: 
        "a b"
        "c d";
}

.card {
    display: flex;
    /* width: 40rem; */
    width: min(40rem, 40vw);
    font-family: 'DM Sans', sans-serif;
}

.card-title {
    /* font-size: 2rem; */
    font-size: min(2rem, 3vw);
    font-weight: 700;

    background: linear-gradient(270deg, #DBA6F4 11.87%, #A981FE 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
}

.card-subtitle {
    /* font-size: 1.5rem; */
    font-size: min(1.5rem, 2vw);
}

.card-icon {
    /* width: 5rem;
    margin-right: 1rem; */
    width: min(5rem, 7vw);
    margin-right: min(1rem, 1vw);
}

@media (max-width: 600px) {
    .courses-main {
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }

    .card {
        width: 60vw;
        margin: 1rem 0;
    }
    
    .card-title {
        font-size: 4vw;
    }
    
    .card-subtitle {
        font-size: 3vw;
    }
    
    .card-icon {
        width: 8vw;
        margin-right: 1vw;
    }
}