.experience {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.experience-arrow {
    width: min(25rem, 40vw);
}

.experience-title, .experience-subtitle {
    /* font-size: 3rem;
    margin-top: 1rem; */
    font-size: min(3rem, 5vw);
    margin-top: min(1rem, 1vw);
    font-family: 'DM Sans', sans-serif;
    background: linear-gradient(270deg, #BFF0FF 11.87%, #82B4FF 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
}

.experience-subtitle {
    font-size: min(1.5rem, 2.5vw);
}

.experience-main {
    /* width: 60rem;
    margin: 4rem 0 2rem 0; */
    width: min(60rem, 80vw);
    margin: min(4rem, 4vw) 0 min(2rem, 2vw) 0;
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 700;
    font-size: min(2rem, 4vw);
    text-align: center;
}

.experience-main p  {
    margin: min(1rem, 1vw) 0;
}