.education {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.education-header {
    display: flex;
    /* margin: 0.5rem 0;
    padding: 1rem; */
    margin: min(0.5rem, 0.5vw) 0;
    padding: min(1rem, 1vw);
    justify-content: center;
    align-items: flex-start;
}

.education-header h2 {
    margin: 2rem 0 0 0;
    font-size: min(4.8rem, 8vw);
}

.education-header img {
    /* width: 10rem; */
    width: min(10rem, 20vw);
}

.education-arrow {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    /* width: 80rem; */
    width: min(80rem, 100vw);
}

.education-arrow-text {
    position: absolute;
    top: min(10rem, 12vw);
    right: 0;

    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 400;
    /* font-size: 1.4rem;
    line-height: 3.2rem;
    width: 20rem; */
    font-size: min(1.4rem, 1.8vw);
    line-height: min(3.2rem, 3.2vw);
    width: min(20rem, 24vw);

    /* Gradient/Sky */
    background: linear-gradient(270deg, #BFF0FF 11.87%, #82B4FF 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
}

.education-arrow-img {
    /* width: 40rem; */
    width: min(40rem, 50vw);
}

.education-main {
    width: min-content;
    display: flex;
    justify-content: center;
    align-items: center;
}

.education-main img {
    /* width: 45rem; */
    width: min(45rem, 45vw);
}

.education-card {
    /* margin: 2rem 0; */
    margin: min(2rem, 2vw) 0;
    font-family: 'DM Sans', sans-serif;
    font-style: normal;
    font-weight: 700;
    width: 40vw;
}

.education-card h4 {
    /* font-size: 4rem; */
    font-size: min(4rem, 4vw);
    word-wrap: break-word;
    background: linear-gradient(270deg, #BFF0FF 11.87%, #82B4FF 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
}

.education-card p {
    /* font-size: 2rem; */
    font-size: min(2rem, 2vw);
}

@media (max-width: 600px) {
    .education-main {
        flex-direction: column;
    }

    .education-main img {
        width: 80vw;
    }

    .education-card {
        width: 60vw;
        margin: 3rem 0;
    }

    .education-card h4 {
        font-size: 5vw;
    }

    .education-card p {
        font-size: 3vw;
        line-height: 1.5rem;
    }
}