.projects {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.projects-header {
    margin: 2rem 0;
}

.projects-arrow {
    width: min(45rem, 45vw);
}

.project {
    display: flex;
    justify-content: center;
    align-items: flex-start;
}

.project-main {
    /* margin: 2rem 0.5rem; */
    margin: min(2rem, 2vw) min(0.5rem, 0.5vw);
    font-family: 'DM Sans', sans-serif;
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 700;
    /* font-size: 2rem;
    width: 58rem; */
    font-size: min(2rem, 2vw);
    width: min(58rem, 43vw);
}

.project-tech-stack {
    /* margin: 0.5rem 0 1rem 0; */
    margin: min(0.5rem, 0.5vw) 0 min(1rem, 1vw) 0;
}

.project-desc {
    font-weight: 400;
}

.project-desc p {
    /* margin: 2rem 0; */
    margin: min(2rem, 2vw) 0;
}

.project-title {
    /* font-size: 4rem; */
    font-size: min(4rem, 4vw);
    /* background: linear-gradient(270deg, #FF4646 11.87%, #FF9486 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent; */
}

.project-img {
    /* margin: 2rem 0.5rem;
    width: 55rem; */
    margin: min(2rem, 2vw) min(0.5rem, 0.5vw);
    width: min(55rem, 50vw);
}

@media (max-width: 600px) {
    .project {
        flex-direction: column;
    }

    .project.reverse {
        flex-direction: column-reverse;
    }

    .project-main {
        font-size: 4vw;
        width: 80vw;
    }

    .project-title {
        font-size: 6vw;
    }
    
    .project-img {
        width: 80vw;
    }
}