.hero {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.hero-text {
    font-family: 'Space Mono';
    font-style: normal;
    font-weight: 700;
    /* font-size: 6.6rem; */
    /* line-height: 12.8rem; */
    font-size: min(6.6rem, 10vw);
    line-height: min(12.8rem, 16vw);
    text-align: center;

    background: linear-gradient(270deg, #DBA6F4 11.87%, #A981FE 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
}

.hero-swiggle {
    /* width: 68.4rem; */
    width: min(68.4rem, 100vw);
}