#nav-container {
    height: 10rem;
}

#click-here {
    position: absolute;
    top: 2rem;
    left: 50vw;
    transform: translateX(5rem);
    width: min(16rem, 35vw);
    z-index: -1;
}

.navbar {
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 1.6rem;
}

.fab-btn:hover {
    cursor: pointer;
    transform: scale(1.1);
}

.fab-icon {
    width: min(10rem, 25vw);
}

.nav-btn {
    pointer-events: none;
    opacity: 0;
    transition: opacity 0.3s;
}

.nav-btn:hover {
    cursor: pointer;
}

.navbar.open .nav-btn {
    pointer-events: all;
    opacity: 1;
}

.nav-btn-icon {
    width: min(8rem, 18vw);
}

/* Floating Navbar */
.navbar.float {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    position: fixed;
    right: min(0.1rem, 0.1vw);
    bottom: min(0.1rem, 0.1vw);
}

.navbar.float .fab-icon {
    width: min(10rem, 15vw);
}

.navbar.float .nav-btn-icon {
    width: min(6rem, 12vw);
}