.footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.footer-swiggle {
    width: min(58.4rem, 80vw);
}

.footer-main {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}

.footer-card {
    font-family: 'DM Sans';
    font-style: normal;
    font-weight: 700;
    /* font-size: 2rem; */
    font-size: min(2rem, 10vw);
    text-align: center;

    /* padding: 1rem; */
    padding: min(1rem, 1vw);

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* height: 28rem;
    width: 28rem; */
    width: min(28rem, 55vw);
}

.footer-card a:link {
    background: linear-gradient(270deg, #A9FE81 100%, #C0F0FF 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-fill-color: transparent;
}

.footer-card img {
    /* margin-bottom: 1rem;
    width: 5rem; */
    margin-bottom: min(1rem, 1vw);
    width: min(5rem, 45vw);
}

@media (max-width: 950px) {
    .footer-main {
        flex-direction: column;
    }

    .footer-card {
        height: 20vw;
        width: 80vw;
    }
}

@media (max-width: 600px) {
    .footer-card {
        height: 20vw;
        width: 60vw;
        margin: 10vw 0 20vw 0;
    }
}